import VCheckboxInput from './VCheckboxInput'
import QTitle from '../Title'

export default {
  name: 'VCheckbox',
  data () {
    return {
      value_: this.value,
    }
  },
  components: {
    VCheckboxInput,
    QTitle,
  },
  props: {
    data: {
      type: Object,
      validator (val) {
        return val.title && val.options && val.options.every(op => op.label && op.value)
      },
    },
    value: Object,
  },
}
