import mixin from './mixin'
export default {
  name: 'VRadio',
  mixins: [ mixin ],
  props: {
    data: {
      type: Object,
      validator (val) {
        return val.title && val.options && val.options.every(op => op.label && op.value)
      },
    },
    value: String,
  },
}
