import hooks from './hooks'
export default {
  name: 'Page',
  data () {
    return {
      current: false,
    }
  },
  computed: {
    questions () {
      return this.$children
    },
    valid () {
      let validity = this.questions.every(q => q.valid)
      hooks.emit('page:validate', [ this, v => validity = v ])
      return validity
    },
  },
}
